.defaultHeader{
    transform: translate(-155px, 0%);
    transition-duration: 500ms;
    display: flex;
    flex-direction: row;
}
.defaultHeader:hover {
    transform: translate(0px, 0%);
    transition-duration: 500ms;
    display: flex;
    flex-direction: row;
}
.links {
    counter-reset: li;
    list-style: none;
    font: 14px "Trebuchet MS", "Lucida Sans";
    padding: 0;
    text-shadow: 0 1px 0 rgba(255,255,255,.5);
    margin-left: 3px;
}
